.footerWrapper {
    padding: 40px 60px;
    display: flex;
    gap: 30px;
    flex-direction: column;
}
.footer{
    background-color: #37dc98;
}
.linkedinIcon img{
    width: 100%;
}
.linkedinIcon{
    width: 30px;
}
.linkedinIcon:hover{
    cursor: pointer;
}
.footerImages img {
    width: 100%;
}

.footerImages {
    width: 447px;
}
.phoneNumber span {
    color: #474646;
    font-weight: 500;

}
.locationSection {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
}
.tagLine,.Location,.contactUS{
    flex-grow: 1;
}
.introduceSection {
    display: flex;
}
.footerdeafultAlign {
    display: flex;
    flex-direction: column;
    gap: 12px;
    flex-grow: 1;
}
.companyReserved {
padding: 20px 0;
}
.companyReserved hr {
    margin: 0;
    border: 1px solid white;
    stroke: none;
}
.phoneNumber{
    display: flex;
    gap: 30px;
}
.Location{
    display: flex;
    justify-content: center;
}