.workSectionWrapper {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
}

.sectionTitle h2{
    font-size: 52px;
}
.workingCategiory span {
    margin-top: 12px;
    display: block;
    margin-right: 12px;
    padding: 0.8rem 1.2rem;
    box-sizing: border-box;
    box-sizing: border-box;
    width: fit-content;
    background: rgba(0,0,0,5%);
    border-radius: 113px;
    color: rgba(0,0,0,.6);
    font-weight: 700;
}
.workingCategiory {
    display: flex;
    flex-wrap: wrap;
}

.sectionTitle {
    margin-bottom: 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}
.workSectionImage {
    width: 500px;
    margin-bottom: 4rem;
}
.workSectionDetails h1 {
font-size: 29px;
margin-bottom: 1rem;
}
.workSectionImage img {
    width: 100%;
}
.sectionTitle a {
    font-size: 30px;
    color: #bd00ff;
    font-weight: 700;
}

a.dribbleButton {
    display: flex;
    width: 100%;
    text-decoration: none;
    background: #bf73ff;
    display: jus;
    justify-content: center;
    align-items: center;
    border-radius: 42px;
    padding: 12px 0px;
    box-sizing: border-box;
    color: white;
}